import React from 'react';
import '../css/Home.css';
import { Link } from 'react-router-dom';
import TileOneImage from '../Assets/Images/BGImage.jpg';
import { FaStore, FaCartPlus } from 'react-icons/fa';
import { CgAdd, CgMathMinus } from 'react-icons/cg';
import samosa from '../Assets/Images/Samosa.jpg';
import pizza from '../Assets/Images/Pizza-from.jpg';
import burger from '../Assets/Images/Burger.jpg';
import healthy from '../Assets/Images/Healthy.jpg';

const Blogs = () => {
	return (
		<div className="home col-lg-12 flex">
			<div className="col-lg-12" style={{ display: 'flex' }}>
				<div className="col-lg-6 col-xs-6"><FaStore /><h5>All that you want!</h5></div>
				<div className="col-lg-6 col-xs-6"><h5>Total Items = 0</h5><FaCartPlus /></div>
			</div>
			<div className="col-lg-12 col-xs-12 food-type">
				<div className="col-lg-12 col-xs-10">
					<div className="card">
						<Link to="/egapp/menu"><img className="card-img-top" src={samosa} alt="Card image cap" /></Link>
						<div className="card-body">
							<h5 className="card-title">Aloo Samosa</h5>
							<CgAdd style={{ float: 'right' }} /><p style={{ float: 'right' }}>0</p><CgMathMinus style={{ float: 'right' }} />
						</div>
					</div>
				</div>
				<div className="col-lg-12 col-xs-12">
					<div className="card" >
						<Link to="/egapp/menu"><img className="card-img-top" src={pizza} alt="Card image cap" /></Link>
						<div className="card-body">
							<h5 className="card-title">Chicken Keema Pizza</h5>
							<CgAdd style={{ float: 'right' }} /><p style={{ float: 'right' }}>0</p><CgMathMinus style={{ float: 'right' }} />
						</div>
					</div>
				</div>
				<div className="col-lg-12 col-xs-12">
					<div className="card" >
						<Link to="/egapp/menu"><img className="card-img-top" src={burger} alt="Card image cap" /></Link>
						<div className="card-body">
							<h5 className="card-title">KFC Burger</h5>
							<CgAdd style={{ float: 'right' }} /><p style={{ float: 'right' }}>0</p><CgMathMinus style={{ float: 'right' }} />
						</div>
					</div>
				</div>
				<div className="col-lg-12 col-xs-12">
					<div className="card" >
						<Link to="/egapp/menu"><img className="card-img-top" src={healthy} alt="Card image cap" /></Link>
						<div className="card-body">
							<h5 className="card-title">Fruit Salad</h5>
							<CgAdd style={{ float: 'right' }} /><p style={{ float: 'right' }}>0</p><CgMathMinus style={{ float: 'right' }} />
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Blogs;
