import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import scooter from '../Assets/Images/delivery-motorcycle.jpg';
import '../css/Header.css';
import contact from '../Assets/Images/h_phone_bg.png';
import samosa from '../Assets/Images/Samosa.jpg';
import pizza from '../Assets/Images/Pizza-from.jpg';
import burger from '../Assets/Images/Burger.jpg';
import healthy from '../Assets/Images/Healthy.jpg';
import { Link } from 'react-router-dom';
import { TiDirections } from 'react-icons/ti'

const responsive = {
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3,
        slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2,
        slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2,
        slidesToSlide: 1 // optional, default to 1.
    }
};
function MyCarousel() {
    return (
        <div className="col-12 brand-header" style={{ background: 'white' }}>

            <div className="col-lg-12 food-header">
                <div className="col-lg-6 category"><TiDirections /><h5>Categories</h5></div>
                <div className="col-lg-6 see-all"><h5><Link to="/egapp/menu">See all</Link></h5></div>
            </div>

            <Carousel autoPlay={true} arrows={true} showDots={true} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsive} >
                <div className="card">
                    <Link to="/egapp/menu"><img className="card-img-top" src={samosa} alt="Card image cap" /></Link>
                    <div className="card-body">
                        <h5 className="card-title">India Snacks</h5>
                        <p className="card-text">We got your back Berhampuriyas!</p>
                    </div>
                </div>
                <div className="card" >
                    <Link to="/egapp/menu"><img className="card-img-top" src={pizza} alt="Card image cap" /></Link>
                    <div className="card-body">
                        <h5 className="card-title">Pizza</h5>
                        <p className="card-text">Dip in to Pizzas like never before!</p>
                    </div>
                </div>
                <div className="card" >
                    <Link to="/egapp/menu"><img className="card-img-top" src={burger} alt="Card image cap" /></Link>
                    <div className="card-body">
                        <h5 className="card-title">Burger</h5>
                        <p className="card-text">Stuff it as you like!</p>
                    </div>
                </div>
                <div className="card" >
                    <Link to="/egapp/menu"><img className="card-img-top" src={healthy} alt="Card image cap" /></Link>
                    <div className="card-body">
                        <h5 className="card-title">Healthy Food</h5>
                        <p className="card-text">Keep that muscle in check!</p>
                    </div>
                </div>
            </Carousel>
        </div>
    )
}


export default MyCarousel;