import React from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import { FormControl, Button } from 'react-bootstrap';
import { useLocation } from "react-router-dom";
import { FaPizzaSlice, FaUtensils, FaSearch, FaMapMarkerAlt } from 'react-icons/fa';
import './Navbar.css';
import { CgShoppingCart } from 'react-icons/cg'

const Navigation = () => {
  //assigning location variable
  const location = useLocation();

  //destructuring pathname from location
  const { pathname } = location;

  //Javascript split method to get the name of the path in array
  const splitLocation = pathname.split("/egapp/");

  return (
    <header >
      <div className="col-lg-12 nav-header">
        <div className="col-lg-4 greet-header-text">
          <h5>Oh MY Good food ! <CgShoppingCart /></h5>
        </div>
        <div className="search-btn col-lg-6 col-md-6 col-xs-10 col-sm-10">
          <Button className=""><FaSearch /></Button>
          <input type="text" className="form-control" placeholder="Search your OMGF!" />
        </div>
        <div className="col-lg-2 col-md-2 col-xs-4 col-sm-4 location">
          <FaMapMarkerAlt style={{ marginTop: '5px', marginBottom: '2%' }} /><p>Your location..</p>
        </div>
      </div>
    </header>
  )
}

export default Navigation;